.doc-profile-container {
    width: 100vw;
    background-color: #56CCF2;
    margin-top: 110px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 90px;
    color: white;
    padding-bottom: 30px;
}

.navigations {
    display: flex;
    gap: 20px;
    margin-top: 20px;
    padding-left: 190px;
    margin-bottom: -70px;
}

.navigations .navigation {
    display: flex;
    align-items: center;
    gap: 20px;
}

.navigations .navigation:last-child {
    font-weight: bold;
}

.doc-profile .doc-profile-info  {
    display: flex;
    gap: 20px;
    margin-left: 170px;
}

.duration-and-price {
    position: absolute;
    right: 140px;
}

.duration-and-price .price {
    font-weight: bold;
}

.duration-and-price .duration {
    font-weight: 600;
}

.information-container .inf .highlight-blue-color {
    color: #56CCF2;
    font-weight: bold;
}

.information-container {
    margin-left: 120px;
    padding: 80px;
    margin-bottom: 200px;
}

.all-informations {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 600px;
}

.all-informations .inf .inf-heading {
    display: flex;
    align-items: center;
    gap: 20px;
}